<template>
  <div class="home">
    <div class="top"></div>
    <div class="content">
      <div style="color:white;font-size:20px;">欢迎来到新安农商银行</div>
      <!-- <div class="nav"></div> -->
      <div class="login-chunk">
        <van-cell-group>
          <img src="../../assets/imgs/personal/图层 5.png" alt />
          <van-field label="昵称" placeholder="请输入昵称" v-model="temp.nick_name" type="text" />
        </van-cell-group>
        <van-cell-group>
          <img src="../../assets/imgs/personal/图层 2.png" alt />
          <van-field label="手机号" placeholder="请输入手机号" v-model="temp.phone" type="text" />
        </van-cell-group>

        <van-cell-group>
          <img src="../../assets/imgs/personal/图层 4.png" alt />
          <van-field
            v-model="temp.verificationCode"
            center
            clearable
            label="验证码"
            placeholder="请输入验证码"
          >
            <van-button
              slot="button"
              size="small"
              type="primary"
              @click="send()"
              :disabled="!disabled"
            >{{codetext}}</van-button>
          </van-field>
        </van-cell-group>

        <van-cell-group>
          <img src="../../assets/imgs/personal/图层 6.png" alt style="width:18px;height:15px;" />
          <van-field label="推荐人推荐码" placeholder="请输入上级六位推荐码" v-model="temp.r_code" type="text" />
        </van-cell-group>
        <!-- <van-cell-group>
          <img src="../../assets/imgs/personal/图层 2.png" alt />
          <van-field label="推荐人手机号" placeholder="请输入上级手机号" v-model="temp.p_phone" type="text" />
        </van-cell-group> -->
        <van-cell-group>
          <img src="../../assets/imgs/personal/图层 3.png" alt />
          <!-- <img src="@/assets/images/home/icon_mima1.png" alt /> -->
          <van-field label="登录密码" placeholder="请输入密码" v-model="temp.password" type="password" />
        </van-cell-group>
        <van-cell-group>
          <img src="../../assets/imgs/personal/图层 3.png" alt />
          <!-- <img src="@/assets/images/home/icon_mima1.png" alt /> -->
          <van-field
            label="确认登录密码"
            placeholder="请确认密码"
            v-model="temp.confirmPassword"
            type="password"
          />
        </van-cell-group>
        <van-cell-group>
          <img src="../../assets/imgs/personal/图层 3.png" alt />
          <!-- <img src="@/assets/images/home/icon_mima1.png" alt /> -->
          
          <van-field label="交易密码"         @input='handleInpute'     maxlength='6' placeholder="请输入密码" v-model="temp.transaction" type="password" />
        </van-cell-group>
        <van-cell-group>
          <img src="../../assets/imgs/personal/图层 3.png" alt />
          <!-- <img src="@/assets/images/home/icon_mima1.png" alt /> -->
          <van-field
            label="确认交易密码"
                    @input='handleInputer'
                     maxlength='6'
            placeholder="请确认密码"
            v-model="temp.confirmPransaction"
            type="password"
          />
        </van-cell-group>
        <!-- <div class="bottom">
        <div @click="go('register')">忘记密码</div>-->
        <!-- <div class='alignx'>|</div> -->
        <!-- <div @click="go('reg')">现在注册 </div> -->
        <button class="button" @click="submit()">注册</button>
        <p class="toReg" @click="go('login')">
          已有账号?
          <span style="color:#28A6FF;">去登录</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { reg } from "@/api/login";
import { sender } from "@/api/personal";

export default {
  data() {
    return {
      checked: true,
      disabled: true,
      codetext: "获取",
      temp: {},
      activeIndex: 0, //规格默认
      slides: [],
      show: false,
      goodsValue: 1,
      goodsId: "",
      goodsInfo: {},
      type:1,
      freight: "",
      goodsSize: [],
      chooseSize: {},
      reduceList: [] //满减列表
    };
  },
  created() {
    if (this.$route.query.uuid) {
      this.temp.r_code = this.$route.query.uuid;
      this.type=2
    //   https://fir.im/a86h
    }
  },
  methods: {
        handleInpute(e){
    this.temp.transaction=e.replace(/[^\d]/g,'');
},
       handleInputer(e){
    this.temp.confirmPransaction=e.replace(/[^\d]/g,'');
},
    countDown(time) {
      if (time === 0) {
        this.disabled = true;
        this.codetext = "获取";
        return;
      } else {
        this.disabled = false;
        this.codetext = "重新发送(" + time + ")";
        time--;
      }
      setTimeout(() => {
        this.countDown(time);
      }, 1000);
    },
    send() {
      sender({ phone: this.temp.phone }).then(res => {
        this.countDown(60);
      });
    },
    submit() {
       console.log(this.type)


     reg(this.temp).then(res => {
        // if (res.errcode == 0) {
            if(this.type==1){
            this.$toast(res.msg);
            this.temp={}
                }else{
                        this.$toast(res.msg);
                        window.location.href = "https://fir.im/a86h";
                }
        // } else {
            this.$toast(res.msg);
        // }
        });

   
    },
    go(res) {
      this.$router.push({ name: res });
    },
    goPay() {
      this.$router.push({
        path: "payOrder",
        query: {
          type: 1
          //   goodsInfo: this.goodsInfo,
          //   goodsNum: this.goodsValue,
          //   chooseSize: this.chooseSize
        }
      });
    }
  }
};
</script>
<style lang="less" scoped>
