  display: flex;
  align-items: center;
  height: 50px;

  img {
    width: 18px;
    height: 20px;
  }
}

.toReg {
  color: #666666;
  height: 40px;
  line-height: 40px;
  border-radius: 0 0 15px 15px;
  font-size: 13px;
  text-align: center;
  // position: absolute;
  // bottom: 20px;
  // left: 50%;
  // transform: translateX(-50%);
}
.chooseWarp /deep/.van-checkbox__icon--checked .van-icon {
  color: black;
  background-color: transparent;
  border-color: black;
}
.chooseWarp /deep/.van-checkbox__icon--checked .van-icon {
  border-radius: 4px;
}
/deep/.van-checkbox__icon--round .van-icon {
  border-radius: 4px;
}
