  color: black;
}
.button {
  width: 270px;
  height: 40px;
  background: #28a6ff;
  // letter-spacing:15px;
  word-spacing: 5px;
  border-radius: 20px;
  font-size: 14px;
  border: none;
  color: white;
  display: block;
  margin: 0 auto;
  margin-top: 15px;
}
.bottom {
  color: #cecece;
  width: 100%;
  margin: 15px auto;
  font-size: 11px;
  display: flex;
  justify-content: space-between;
}
.login-chunk {
  background: white;
  padding: 20px;
  border-radius: 20px;
  margin-top: 38px;
}
.card {
  height: 35px;
  padding: 10px 0;
  background: #f8f8f8;
  border-radius: 20px;
  margin-top: 15px;

  img {
    width: 18px;
    margin-top: 8px;
    margin-left: 12px;
  }
  .input {
    width: 140px;
    height: 20px;
    margin-left: 10px;
    background: #f8f8f8;
    font-size: 14px;
  }
  // &:nth-child(3) {
  //   border-radius: 0px 0px 5px 5px;
  // }
}
.home {
  width: 100%;
  height: 100%;
  background: #f8f5f0;
}
.top {
  width: 100%;
  height: 200px;
  background: url("../../assets/imgs/personal/back.png") no-repeat;
  background-size: 100% 100%;
}
.content {
  width: 90%;
  margin: 0 5%;
  margin-top: -140px;
}
.nav {
  width: 100%;
  border-radius: 20px;
  background: white;
  padding: 15px 0;
}
</style>
